body {
    margin: 0;
    font-family: 'Arial', sans-serif;
  }
  
  .profile-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(160deg, #9DDED3, #D4EAC4);
  }
  
  .profile-card {
    background: white;
    padding: 40px;
    border-radius: 16px;
    width: 90%;
    max-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    position: relative;
    text-align: left;
  }
  
  .profile-card h1 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .name-input {
    width: 100%;
    font-size: 1.2rem;
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    border-bottom: 2px solid gray;
    outline: none;
  }
  
  .subtitle {
    font-size: 0.8rem;
    color: #555;
    margin-bottom: 20px;
  }
  
  .continue-button {
    width: 100%;
    background: linear-gradient(to right, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1);
    color: white;
    border: none;
    padding: 15px;
    font-weight: bold;
    font-size: 1rem;
    border-radius: 25px;
    cursor: pointer;
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    left: 10px;
    border: none;
    background: transparent;
    font-size: 1.5rem;
    cursor: pointer;
  }
  